<template>
  <div class="tabs">
    <ul>
      <li v-for="day in days" :class="[activeIndex === day ? 'is-active' : '']">
        <a @click="setActive(day)">{{ day[0].toUpperCase() + day.slice(1) }}</a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Navigation',
  props: {
    lessons: Array,
  },
  data() {
    return {
      days: ['montag', 'dienstag', 'mittwoch', 'donnerstag', 'freitag'],
      activeIndex: 'montag'
    }
  },
  methods: {
    setActive(day) {
      this.activeIndex = day
      this.$emit('filter-monday', day)
    }
  }
}
</script>

