<template>
  <div class="block">
    <button v-for="semester in semesters" @click="clickSemester(semester)" class="button is-small">{{semester}}. Semester</button>
    <button v-for="type in types" @click="$emit('filter-type', type.slice(0, 2)); filterSet = true" class="button is-small" :class="[type.slice(0, 2) === 'Ph' ? 'blue' : [type.slice(0, 2) === 'Ge' ? 'orange' : 'green']]">{{type}}</button>
    <button v-if="filterSet" @click="$emit('reset-filter'); filterSet = false" class="button is-dark is-small">Reset</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      semesters: [1, 2, 3, 4],
      types: ['Philosophie', 'Kunst', 'Gesellschaft'],
      filterSet: false
    }
  },
  methods: {
    clickSemester(semester) {
      this.$emit('filter-semester', semester)
      this.filterSet = true
    }
  },
  name: 'FilterButtons'
}
</script>

<style scoped>
  button {
    margin-right: 1rem;
  }
.blue {
  border-color: #447ce8;
  color: #447ce8;
}

.green {
    border-color: #e85344;
  color: #e85344;
}

.orange {
    border-color: #96b5af;
  color: #96b5af;
}
</style>